@import url(https://fonts.googleapis.com/css?family=Roboto);
@import url(https://fonts.googleapis.com/css2?family=Quicksand&display=swap);
header {
	width: 100vw;
	margin-bottom: 15px;
	display: -webkit-flex;
	display: flex;
	-webkit-align-items: center;
	        align-items: center;
	-webkit-justify-content: center;
	        justify-content: center;
}

.__nav .active li {
	color: var(--main-color) !important;
}

@media (max-width: 600px) {
	.first_nav_element {
		padding-left: 0;
	}
	.__nav {
		padding-left: 0 !important;
	}
}

.__nav {
	margin-top: 30px;
	list-style-type: none;
}

.__nav li {
	display: inline-block;
	padding: 0 20px;
	color: #fff;
	text-decoration: none;
	transition: all 0.3s ease 0s;
}

.__nav li:hover {
	color: var(--main-color);
}


.toggle_button .bar {
	height: 3px;
	width: 100%;
	background-color: #fff;
	border-radius: 10px;
}

.toggle_button {
	position: fixed;
	top: .75rem;
	right: 1rem;
	display: none;
	-webkit-flex-direction: column;
	        flex-direction: column;
	-webkit-justify-content: space-between;
	        justify-content: space-between;
	width: 30px;
	height: 21px;
}

.toggle_button:hover {
	cursor: pointer;
}

@media (max-width: 500px) {
	.toggle_button {
		display: -webkit-flex;
		display: flex;
	}

	.__nav {
		display: -webkit-flex;
		display: flex;
		-webkit-flex-direction: column;
		        flex-direction: column;
		position: fixed;
		border-radius: 15px;
		border: 2px solid #fff;
		top: 7.5rem;
		left: -8.75rem;
		padding: 5px 15px 5px 0;
		background-color: rgb(39, 39, 39);
		transition: -webkit-transform .4s;
		transition: transform .4s;
		transition: transform .4s, -webkit-transform .4s;
	}

	.visible_navbar {
		-webkit-transform: translateX(140px);
		        transform: translateX(140px);
	}

	.__nav li {
		padding: 15px;
		width: 100%;	
		margin: 1px;
		margin-left: 0;
		font-size: 1.5rem;
	}

	.first_nav_element {
		padding: inherit;
	}


}


.footer_border {
	margin: auto;
	width: 97vw;
	border-radius: 50px;
	background-color: #fff;
	text-align: center;
}

.footer_links {
	display: -webkit-flex;
	display: flex;
	-webkit-flex-direction: row;
	        flex-direction: row;
	-webkit-justify-content: center;
	        justify-content: center;
}

.footer_links a {
	font-size: 1.5rem;
	opacity: .5;
	margin: 0 5px;
	color: inherit;
	text-decoration: none;
	transition: opacity ease-in 0.3s;
}

.footer_links a:hover {
	cursor: pointer;
	opacity: 1;
}

@media (max-width: 500px) {
	.footer_links a {
		margin: 0 7px;
		font-size: 2.5rem;
	}
}

.copyright {
	font-family: 'Source Code Pro', monospace;
}

._404 h1 {
	font-size: 3rem;
	color: red;
	font-family: 'Gill Sans', 'Gill Sans MT', Calibri, 'Trebuchet MS', sans-serif;
}

._404 {
	text-align: center;
}

.introduction {
	text-align: center;
	margin-top: 120px;
	margin-bottom: 265px;
}

.introduction h1 {
	font-family: 'Roboto Thin';
	margin: 0 0 10px;
	font-weight: 100;
}

@media (max-width: 400px) {
	.introduction {
		margin-bottom: 355px;
	}
}

.introduction_buttons {
	display: -webkit-flex;
	display: flex;
	-webkit-justify-content: center;
	        justify-content: center;
	-webkit-flex-wrap: wrap;
	        flex-wrap: wrap;
}

.introduction_buttons button {
	font-family: 'Roboto Thin';
	color: #fff;
	background-color: #ff7a7a;
	border: 1px solid rgb(131, 131, 131);
	font-size: 1.1rem;
	border-radius: 2px;
	margin: 0 7px;
	transition: font-size ease 50ms;
}

.introduction_buttons button:hover {
	cursor: pointer;
	background-color: rgb(211, 180, 180);
	color: black;
	font-size: 1.2rem;
}

@media (max-width: 500px) {
	.introduction_buttons button {
		margin-bottom: 5px;
		font-size: 1.5rem;
	}
	.introduction_buttons button:hover {
		font-size: 1.6rem;
	}
}

.ThankYou {
	text-align: center;
}

.ThankYou h1 {
	color: lightgreen;
}

@media (max-width: 500px) {
	.ThankYou {
		margin-top: 200px;
		margin-bottom: 250px;
	}
}

:root {
	--main-color: rgb(0, 140, 255);
	--background-color: rgb(39, 39, 39);
}

body {
	box-sizing: border-box;
	background-color: rgb(39, 39, 39);
	background-color: var(--background-color);
	color: #fff;
	font-family: Quicksand;
	display: -webkit-flex;
	display: flex;
	-webkit-flex-direction: column;
	        flex-direction: column;
	margin: 0;
	overflow-x: hidden;
	padding: 0;
}

